.nav__list-logo {
  color: rgba(51, 39, 0, 0.8509803922);
  font-size: 1.5rem;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  padding: 0;
  margin: 0;
}

img {
  width: 100%;
  height: 100%;
}

.btn {
  background-color: #FFCC54;
  border: none;
}

.btn__sm {
  box-shadow: 0px 0.5px 3px rgba(51, 39, 0, 0.5);
  border-radius: 0.5rem;
  padding: 0.8rem 1.5rem;
}

.container {
  width: 360px;
  border: 2px solid red;
  display: flex;
  justify-content: center;
  margin: auto;
}

.nav {
  border: 2px solid red;
  position: relative;
}

.nav__lists {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 3rem;
}

.nav__list-logo {
  margin-right: auto;
}

.section-1 {
  display: flex;
  position: absolute;
  top: 0;
  height: 300px;
  border: 2px solid red;
  z-index: -1;
}

.section-1__text {
  margin-top: 5rem;
}

.section-1__img {
  border: 2px solid red;
}